#contact-list {
  position: absolute;
  bottom: 0; 
  margin-left: -2em;
  margin-bottom: 1em;
  list-style: none; 
}

.contact-icons { 
  -webkit-filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg) brightness(102%) contrast(102%); 
          filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg) brightness(102%) contrast(102%); /* White Text */
  transition: 2s;
  height: 2em;
}

.contact-icons:hover { 
  -webkit-filter: invert(69%) sepia(66%) hue-rotate(161deg) brightness(94%) contrast(102%); 
          filter: invert(69%) sepia(66%) hue-rotate(161deg) brightness(94%) contrast(102%);
  transition: 2s;
}

.contact-link{
  text-decoration: none;
  color: white;
}

.contact-link a { 
  text-decoration: none;
}
.contact-link a:hover { 
  background-color: rgba(159, 187, 187, 0);
}
.app-about-title { 
    font-family: 'NeueMontreal', sans-serif;
    font-weight: 900;
    font-size: 4.2em;
    margin: 0.2em;
    text-align: left;
}

.app-about-text {
    font-size: 1em;
