}
.project-box { 
    width: 50em; 
    max-width: 100%;
    height: 15em;

    background-color: rgba(255, 255, 255, 0.1);
    
    border-radius: 1px;
    border-color: white;
    border-style: solid;
    border-width: 0.01em;


    color: white;
    text-align: left;

    margin-top: 3em;
    margin-left: 12.5%;
}

@font-face {
    font-family: HelveticaNow;
    src:url("/src/font/HelveticaNow.ttf");
  }

@font-face {
font-family: NeueMontreal;
src:url("/src/font/NeueMontreal-Bold.otf" );
}

.project-box-image { 
    width: 16.5em; 
    height: 16.5em;
    max-width: 100%;
    float: left;

}

.project-box-img { 
    width: 15em; /* 15 em height and width*/
    height: 15em;
    float: left;
    background-size: cover; 
    /* Maintain image in div*/
    background-position: center;
    background-repeat: no-repeat;
}
.project-box-content { 
    margin-left: 15em; 
    margin-right: 5em;
    
}
.project-box-title {
    width: 15em; 
    height: 25%; 
    
    position: relative;
    margin-top: 0.4em;
    margin-bottom: 1em;


    font-family: 'NeueMontreal', sans-serif;
    font-size: 1.2em;
    font-weight: bold;

}

@media only screen and (max-width: 800px) {
    .project-box { 
        height: 50%;
        width: 60%;
    }
    .project-box-content { 
        margin-left: 0.4em; 
        margin-right: 0.4em;

    }

    .project-box-title { 
        font-size: 0.8em;
    }
    .project-box-img { 
        /* fit div */
        float: none;
        display: block;
        /* center the image */
        margin-left: auto;
        margin-right: auto;



    }

    .project-box-description { 
        display: none
    }

    .project-box-date { 
        
        /* display below image*/
        position: relative;
        text-align: justify; 
        margin-top: 1em;
        font-size: 0.92em;

    }

    .project-box-image { 

        overflow: auto;
    }


}
.project-box-description {
    font-size: 1em;
    
    position: relative; 
    margin-top: 0.2em;

    font-family: 'Roboto', sans-serif;
}

.project-box-date {
    position: relative; 
    /* text justify  */
    text-align: justify;
    margin-top: 1em;

    font-style: italic;
}

.project-box-link { 
    margin-top: 1em; 
    /* Bold */
    font-family: "NeueMontreal", sans-serif;
    font-weight: 100;
}
@font-face {
    font-family: NeueMontreal;
    src:url("/src/font/NeueMontreal-Bold.otf" );
  }

.app-projects {
    border-bottom: 1px solid #ccc;
    overflow-y: auto;
    overflow-x:hidden;
    height: 96vh;
    scrollbar-width: none;
    scroll-behavior: smooth;
}

.app-projects::-webkit-scrollbar {
    display: none;
}
.app-projects-title { 
    font-family: 'NeueMontreal', sans-serif;
